.public {
  padding-top: 32px;
  padding-bottom: 128px;
}

.breadcrumbs {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 13px;
  a, li {
    line-height: 144%;
    color: #A2A2A2;
    text-decoration: none;
  }
  li {
    position: relative;
    &:not(:last-child) {
      margin-right: 13px;
      &::after {
        content: '/';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: -8px;
      }
    }
  }
}

.public__title {
  color: #252525;
  margin-bottom: 35px;
}

.filters {
  display: flex;
  margin-bottom: 57px;
}

.filter {
  line-height: 120%;
  position: relative;
  &:not(:last-child) {
    margin-right: 28px;
  }
}

.filter__name {
  color: #9E9E9E;
  margin-right: 6px;
}

.filter__btn {
  color: #1C1C1C;
  padding-right: 16px;
  cursor: pointer;
  position: relative;
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    margin-top: -4.5px;
    right: 0;
    background-image: url(../img/arrow-bottom.svg);
    width: 8px;
    height: 9px;
    transition: .3s ease-in-out;
  }
}

.filter__separator {
  margin-left: 8px;
  margin-right: 8px;
}

.public-name {
  margin-top: 0;
  margin-bottom: 50px;
  font-size: 24px;
  line-height: 110%;
  color: #252525;
}

.card {
  display: flex;
  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.card__link {
  margin-right: 40px;
  flex-shrink: 0;
  picture {
    flex-shrink: 0;
    width: 180px;
    height: 180px;
    display: block;
  }
}

.card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}

.card__text {
  font-size: 16px;
  line-height: 144%;
  color: #252525;
  text-decoration: none;
  max-width: 430px;
  display: block;
}

.card__date {
  font-size: 14px;
  line-height: 138%;
  color: #999999;
  margin: 16px 0;
}

.card__tags {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.card__tag {
  font-size: 12px;
  line-height: 100%;
  color: #9E9E9E;
  height: 24px;
  display: inline-flex;
  align-items: center;
  padding: 0 10px;
  border: 1px solid #EAEAEA;
  border-radius: 12px;
  &:not(:last-child) {
    margin-right: 8px;
  }
}

.read__date {
  margin-top: 6px;
  margin-bottom: 6px;
}

.read__text {
  font-size: 16px;
  line-height: 144%;
  color: #1C1C1C;
  text-decoration: none;
}

.read-title {
  margin-top: 0;
  font-size: 16px;
  line-height: 110%;
  color: #252525;
  padding-bottom: 20px;
  border-bottom: 1px solid #E1E1E1;
  margin-bottom: 28px;
}

.read {
  &:not(:last-child) {
    margin-bottom: 28px;
  }
}

.public__btn {
  margin-top: 40px;
}

.public__row {
  &:not(:last-child) {
    margin-bottom: 65px;
  }
}

.lecture-name {
  margin-bottom: 56px;
}

.lecture__text {
  font-size: 16px;
  line-height: 144%;
  color: #1C1C1C;
  text-decoration: none;
}

.lecture__date {
  margin-top: 6px;
  margin-bottom: 20px;
}

.lecture__list {
  padding-top: 20px;
  border-top: 1px solid #252525;
  a {
    color: #1C1C1C;
    font-size: 14px;
    line-height: 144%;
    text-decoration: none;
  }
}

.material {
  display: flex;
}

.material__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  max-width: 140px;
  max-height: 160px;
}

@media(max-width: 991px) {
  .public {
    padding-top: 20px;
  }
  .public__title {
    margin-bottom: 28px;
  }
  .filters {
    flex-wrap: wrap;
    margin-bottom: -10px;
  }
  .filter {
    margin-bottom: 10px;
  }
  .row.public__row {
    margin-top: 55px;
  }
  .public-name {
    margin-bottom: 48px;
  }
}

@media(max-width: 767px) {
  .public__title {
    font-size: 36px;
    line-height: 110%;
  }

  .card {
    display: block;
  }
  .card__link {
    display: block;
    margin-bottom: 20px;
  }
  .card__link picture {
    width: auto;
    height: auto;
  }
  .card__text {
    max-height: 70px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .material__img {
    max-height: 84px;
    max-width: 100px;
  }
  .card__link {
    margin-right: 16px;
  }
}
