.page__body--pp {
  &::after {
    background: linear-gradient(0deg, rgba(28, 28, 28, 0.8), rgba(28, 28, 28, 0.8));
  }
  .page-header {
    padding-top: 40px;
  }
}

.pp {
  padding-top: 113px;
  padding-bottom: 139px;
}

.pp__title {
  margin-bottom: 32px;
}

.pp__text {
  line-height: 150%;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 48px;
}
.pp__text-list {
  display: flex;
  color: rgba(255, 255, 255, 0.5);
  li {
    display: flex;
  }
  li:not(:last-child) {
    &::after {
      content: '∙';
      margin-left: 4px;
      margin-right: 4px;
    }
  }
}

.pp__btns {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  column-gap: 20px;
}

.pp-item {
  max-width: 389px;
  margin-left: auto;
  display: flex;
  margin-right: 25px;
}

.pp-item__link {
  flex-shrink: 0;
  max-height: 110px;
  max-width: 100px;
  img {
    border-radius: 5px;
  }
}

.pp-item__title {
  font-weight: bold;
  font-size: 18px;
  line-height: 120%;
  color: #FFFFFF;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.pp-item__info {
  margin-left: 30px;
}

.pp-item__list {
  margin: 12px 0;
}

.card__tag--grey {
  color: #979797;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.pp-item__list li:not(:last-child)::after {
  content: '∙';
  margin-left: 2px;
  margin-right: 3px;

}
.pp-item__list li {
  display: block;
  max-width: 21ch;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.pp__tags {
  flex-wrap: nowrap;
  .card__tag + .card__tag {
    max-width: 160px;
    span {
      display: block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

  }
  .card__tag {
    &:not(:last-child) {
      margin-right: 4px;
    }
  }
}
.card__tag--grey {
  padding: 0 8px;
}

.pp__right {
  margin-top: 5px;
}

@media (max-width: 991px) {
  .pp {
    padding-bottom: 60px;
  }
  .page__body--pp .main__wrapper {
    max-height: 468px;
  }
  .pp-item {
    margin-left: 0;
    margin-right: 0;
  }
  .pp__title{
    br {
      display: none;
    }
  }
}
@media(max-width: 767px) {
  .pp__title {
    font-size: 32px;
    line-height: 129%;
    margin-bottom: 20px;
  }
  .pp__btns {
    grid-template-columns: 100%;
    column-gap: 0;
    row-gap: 20px;
    .btn {
      width: 100%;
    }
  }
  .pp-item__info {
    margin-left: 20px;
  }
  .pp-item__title {
    font-size: 16px;
  }
  .pp-item {
    max-width: 100%;
  }
  .pp-item__list li {
    max-width: 14ch;
  }
  .pp__tags .card__tag + .card__tag {
    max-width: 93px;
  }
  .page__body--pp .main__wrapper {
    max-height: 450px;
  }
}
