.btn {
  height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: max-content;
  padding: 0 24px;
  font-size: 16px;
  line-height: 110%;
  text-align: center;
  color: #1C1C1C;
  font-weight: bold;
  border-radius: 5px;
  background-color: #fff;
  cursor: pointer;
  transition: .3s;
  margin: 2px;
  &:hover {
    background-color: var(--hover-color);
    color: #fff;
  }
  &:active {
    background-color: var(--active-color);
    color: #fff;
  }
  &:focus {
    box-shadow: 0 0 0 2px var(--hover-color);
    background-color: #fff;
    color: #1C1C1C;
  }
  &:disabled,
  &.disabled {
    color: #9E9E9E;
    pointer-events: none;
  }
  &--black {
    background-color: #252525;
    color: #fff;
    &:focus {
      box-shadow: 0 0 0 2px var(--hover-color);
      background-color: #252525;
      color: #fff;
    }
    &:disabled,
    &.disabled {
      background-color: #F5F5F5;
    }
  }
  &--w100p {
    width: 100%;
  }
}

.btn-more {
  font-size: 16px;
  line-height: 110%;
  color: #252525;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 20px;
  height: 48px;
  border: 1px solid #EAEAEA;
  border-radius: 8px;
  cursor: pointer;
  span {
    margin-right: 12px;
  }
  @media(max-width: 767px) {
    width: 100%;
  }
}

.btn-arrow {
  font-weight: bold;
  font-size: 16px;
  line-height: 110%;
  text-align: center;
  color: #1C1C1C;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 46px;
  padding: 0 24px;
  transition: .3s;
  border-radius: 5px;
  margin: 2px;
  svg {
    margin-left: 6px;
  }
  path {
    transition: .3s;
    stroke: #252525;
  }
  &:hover,
  &:active {
    color: #fff;
    background-color: #252525;
    path {
      stroke: #fff;
    }
  }
  &:focus {
    color: #1C1C1C;
    box-shadow: 0 0 0 2px var(--hover-color);
    background-color: transparent;
  }
  &--white {
    color: #fff;
    path {
      stroke: #fff;
    }
    &:hover,
    &:active {
      color: #252525;
      background-color: #fff;
      path {
        stroke: #252525;
      }
    }
    &:focus {
      color: #fff;
      box-shadow: 0 0 0 2px var(--hover-color);
      background-color: transparent;
      path {
        stroke: #fff;
      }
    }
  }
}
