.page__body--main {
  background: no-repeat center/cover;
  position: relative;
  z-index: 1;
  --color: rgba(28, 28, 28, 0.55);
  &:after {
    content: '';
    position: absolute;
    @include fullSide;
    background-image: linear-gradient(0deg, var(--color), var(--color));
    z-index: -1;
  }
}
.page__body--promo {
  --color: rgba(0, 0, 0, 0.7);
}
.bg-image img {
  position: absolute;
  z-index: -1;
  @include fullSide;
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.logo {
  flex-shrink: 0;
}
.page-header {
  padding-top: 48px;
  &--second {
    padding-bottom: 28px;
    .navigation {
      margin-left: 156px;
    }
    .navigation a {
      color: #000;
      &::after {
        background-color: #000;
      }
    }
    .lang {
      color: #000;
      &::after {
        background-color: #000;
      }
    }
  }
}

.page-header__container {
  @media(min-width: 1200px) {
    max-width: 1190px;
    padding: 0 15px;
    margin: 0 auto;
  }
}
.big-container {
  @media(min-width: 1200px) {
    max-width: 1310px;
    padding: 0 15px;
    margin: 0 auto;
  }
}

.page-header__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation {
  margin-left: 37px;
  ul {
    display: flex;
  }
  li {
    &:not(:last-child) {
      margin-right: 21px;
    }
  }
  a {
    line-height: 120%;
    color: #FFFFFF;
    text-decoration: none;
    position: relative;
    padding-bottom: 14px;
    display: block;
    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: #fff;
      bottom: 0;
      left: 0;
      transition: .3s;
      transform: scaleX(0);
    }
    &:hover, &.current {
      &::after {
        transform: scaleX(1);
      }
    }
  }
}

.search-and-lang {
  display: flex;
  align-items: center;
  color: #fff;
}

.search {
  width: 15px;
  height: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.lang {
  line-height: 112%;
  text-transform: uppercase;
  margin-left: 40px;
  position: relative;
  &::after {
    content: '';
    position: absolute;
    left: -21px;
    top: 50%;
    margin-top: -10px;
    width: 1px;
    height: 20px;
    background-color: #fff;
  }
}

.page-header__left {
  display: flex;
  align-items: center;
}

.hamburger {
  margin-left: 28px;
  cursor: pointer;
}

@media(max-width: 1199px) {
  .page-header--second .navigation {
    margin-left: 50px;
  }
}

@media(max-width: 991px) {
  .navigation {
    display: none;
  }
  .page-header {
    padding-top: 28px;
    padding-bottom: 28px;
  }
}
