.projects {
  padding-top: 114px;
  padding-bottom: 114px;
}

.bg-image__promo img {
  position: fixed;
}

.page__body--pj::after {
  position: fixed;
}

.project__link {
  max-height: 110px;
  max-width: 100px;
  margin-right: 20px;
  flex-shrink: 0;
  img {
    border-radius: 5px;
  }
}

.project {
  max-width: 386px;
  margin-left: auto;
  &:hover {
    .project__hover {
      display: block;
    }
  }
}
.project__top {
  display: flex;
}
.projects__content {
  margin-top: 5px;
}

.project__text {
  line-height: 150%;
  color: #9E9E9E;
  margin-top: 5px;
  p {
    margin: 0;
  }
}

.project__hover {
  margin-top: 15px;
  line-height: 144%;
  color: #9E9E9E;
  padding-top: 16px;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  display: none;
}

@media(max-width: 991px) {
  .projects {
    padding-top: 57px;
    padding-bottom: 80px;
  }
  .project {
    margin-left: 0;
  }
  .project__hover {
    display: block;
  }
  .project__title {
    margin-bottom: 60px;
  }
}
@media(max-width: 767px) {
  .project__title {
    line-height: 129%;
    margin-bottom: 57px;
  }

}
