.main {
  padding-top: 122px;
  padding-bottom: 134px;
}
.title {
  font-size: 46px;
  line-height: 110%;
  color: #FFFFFF;
  margin: 0;
}

.main__title {
  margin-bottom: 48px;
}

.article {
  text-decoration: none;
  display: flex;
  margin-left: auto;
  margin-right: 47px;
  width: max-content;
  position: relative;
  &:hover {
    .article__text::after {
      visibility: visible;
      opacity: 1;
    }
  }
}
.article__info {
  max-width: 230px;
}
.article__image {
  margin-right: 32px;
  max-width: 100px;
  max-height: 110px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}
.article__title {
  font-size: 12px;
  line-height: 110%;
  letter-spacing: 0.22em;
  text-transform: uppercase;
  color: #979797;
  font-weight: bold;
  display: block;
}
.article__text {
  margin-bottom: 0;
  margin-top: 11px;
  font-weight: bold;
  font-size: 18px;
  line-height: 138%;
  color: #FFFFFF;

  &::after {
    content: '';
    background-image: url(../img/arrow.svg);
    width: 24px;
    height: 24px;
    display: inline-block;
    vertical-align: middle;
    margin-left: 5px;
    visibility: hidden;
    opacity: 0;
    transition: .3s;
    margin-top: -1px;
  }
}
.mb40 {
  margin-bottom: 40px;
}
.mb-40 {
  margin-bottom: -40px;
}
@media(max-width: 991px) {
  .main {
    padding-top: 70px;
    padding-bottom: 74px;
  }
  .article {
    margin-left: initial;
    margin-right: 0;
  }
  .main__wrapper {
      max-height: 410px;
      overflow: hidden;
  }
  .title {
    font-size: 45px;
  }
}
@media(max-width: 767px) {
  .article {
    align-items: flex-start;
  }
  .article__image {
    margin-right: 21px;
  }
  .article__text {
    font-size: 16px;
    line-height: 144%;
  }

}
@media(max-width: 575px) {
  .article {
    max-width: 100%;
  }
}
