.filter__btn {
  &.open {
    &::after {
      transform: scaleY(-1);
      margin-top: -3px;
    }

  }
}
.filter__popup {
  transition: .3s ease-in-out;
  visibility: hidden;
  opacity: 0;
}
.filter-popup:not(.date-popup) {
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
  padding: 24px;
  background-color: #FFFFFF;
  box-shadow: 0px 20px 45px rgba(0, 0, 0, 0.07);
  border-radius: 16px;
  width: 300px;
  z-index: 5;
  &.open {
    visibility: visible;
    opacity: 1;
  }
}
.search-wrap {
  position: relative;
  margin-bottom: 24px;
}
.custom-scroll + .btn {
  margin-top: 24px;
}
.filter-label {
  position: relative;
  padding-left: 32px;
  position: relative;
  height: 20px;
  display: block;
  transition: .3s;
  &.open {
    padding-left: 0;
    &::before {
      visibility: hidden;
      opacity: 0;
    }
    + .search-clear {
      visibility: visible;
      opacity: 1;
    }
  }
  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background-image: url(../img/gray-search.svg);
    width: 20px;
    height: 20px;
    transition: .3s;
  }
  input {
    font-size: 14px;
    line-height: 120%;
    height: 20px;
    border: 0;
    padding: 0;
    width: 100%;
    padding-right: 25px;
    &::placeholder {
      color: #A2A2A2;
    }
  }
}
.custom-scroll {
  max-height: 248px;
  overflow-y: auto;
  scrollbar-color: rgba(#252525, .5), #fff;
  scrollbar-width: thin;
  &::-webkit-scrollbar {
    width: 4px;
  }
  &::-webkit-scrollbar-track {
    background: #fff;
  }
  &::-webkit-scrollbar-thumb {
    background: rgba(#252525, .5);
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background:#252525;
  }
}
.search-list {
  button {
    position: relative;
    line-height: 120%;
    color: #252525;
    padding-left: 25px;
    display: block;
    cursor: pointer;
    + * {
      margin-top: 12px;
    }
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 13px;
      height: 11px;
      background-image: url(../img/tick.svg);
      visibility: hidden;
      opacity: 0;
      transition: .3s;
    }
    &.checked {
      &::before {
        visibility: visible;
        opacity: 1;
      }
    }
  }
}

.search-clear {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 5;
  visibility: hidden;
  opacity: 0;
  transition: .3s;
  &.open {
    visibility: visible;
    opacity: 1;
  }
}
.filter-head {
  display: none;
}
.all-filters {
  display: none;
}
.filter-row {
  display: flex;
  flex-wrap: wrap;
}
.filter-btns {
  display: none;
}
.small-popup {
  padding-top: 30px;
  padding-bottom: 30px;
}
@media(max-width: 767px) {
  .all-filters {
    display: block;
  }
  .filters {
    display: block;
    visibility: hidden;
    opacity: 0;
    transition: .3s;

  }
  .filter-wrap {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background: #FFFFFF;
    border-radius: 16px 16px 0px 0px;
    z-index: 10;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
    max-height: 610px;
    height: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
    &.open {
      visibility: visible;
      opacity: 1;
    }
  }
  .filter-head {
    padding-top: 24px;
    padding-bottom: 24px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between
  }
  .filter:not(:last-child) {
    margin-bottom: 28px;
  }
  .filter__head-close {
    cursor: pointer;
  }
  .filter:not(:last-child) {
    margin-right: 0;
  }
  .filter-row {
    flex-direction: column;
  }
  .filter-popup:not(.date-popup) {
    padding: 0 30px;
    padding-bottom: 30px;
    width: 100%;
    position: fixed;
    max-height: 610px;
    height: 100%;
    box-shadow: none;
    top: auto;
    bottom: 0;
    z-index: 1;
    margin-top: 25px;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      height: 122px;
      width: 100%;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 35.39%);
      pointer-events: none;
    }
  }

  .filter__head-back {
    cursor: pointer;
  }
  .filter-form {
    height: 100%;
    margin-top: auto;
  }
  .custom-scroll {
    min-height: 54vh;
  }
  .filter__submit {
    position: relative;
    z-index: 1;
  }
  .filter-btns {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: auto;
  }
  .custom-scroll {
    margin-left: -30px;
    margin-right: -30px;
    padding-left: 30px;
  }
}
