.page-footer {
  padding-bottom: 43px;
  overflow: hidden;
  &--second {
    .page-footer__container {
      &::before {
        border-color: #E1E1E1;
      }
    }
    .copyright,
    .footer-list a,
    .footer-list li:not(:last-child)::after {
      color: #9E9E9E;
    }
    .social__item svg {
      fill: #9E9E9E;
    }
    .copyright path {
      stroke: #9E9E9E;
    }
  }
}

.page-footer__container {
  position: relative;
  padding-top: 21px;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 30px;
    right: 30px;
    border-top: 1px solid rgba(255, 255, 255, 0.13);
  }
}

.copyright {
  display: flex;
  align-items: center;
  font-size: 12px;
  line-height: 137%;
  color: #CACACA;
  transition: .3s;
  path {
    transition: .3s;
  }
  &:hover {
    color: #9E9E9E;
    path {
      stroke: #9E9E9E;
    }
  }

}

.copyright__text {
  margin-right: 6px;
}

.footer-list {
  display: flex;
  margin-top: 6px;
  a {
    font-size: 12px;
    line-height: 138.69%;
    color: #CACACA;
    text-decoration: none;
    transition: .3s;
    &:hover {
      color: #9E9E9E;
    }
  }
  li {
    &:not(:last-child) {
      margin-right: 10px;
      position: relative;
      &::after {
        content: '·';
        position: absolute;
        right: -7px;
        color: #CACACA;
      }
    }
  }
}

.social {
  display: flex;
  width: max-content;
  margin-left: auto;
}
.social__item {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .3s;
  &:not(:last-child) {
    margin-right: 14px;
  }
  svg {
    transition: .3s;
  }
  &:hover {
    svg {
      fill: #9E9E9E;
    }
  }
}
@media(max-width: 991px) {
  .page-footer__container {
    padding-top: 20px;
  }
  .page-footer {
    padding-bottom: 40px;
  }
}
@media(max-width: 767px) {
  .social {
    margin: 0;
    margin-top: 20px;
  }
  .footer-list {
    display: block;
  }
  .footer-list li:not(:last-child)::after {
    content: unset;
  }
  .footer-list li:not(:last-child) a{
    margin-right: 5px;
    position: relative;
    &::after {
      content: '·';
      position: absolute;
      right: -7px;
      color: #CACACA;
    }
  }
  .article__text::after {
    margin-left: -1px;
  }
}
