.date-wrap {
  display: flex;
  position: relative;
}
.filter--date {
  display: flex;
}
.date-popup {
  position: absolute;
  top: 20px;
  right: 0;
  background: #FFFFFF;
  box-shadow: 0px 25px 60px rgba(0, 0, 0, 0.07);
  border-radius: 16px;
  padding: 30px;
  padding-top: 27px;
  transition: .3s ease-in-out;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  &.open {
    visibility: visible;
    opacity: 1;
    pointer-events: all;
  }
}

.flatpickr-calendar {
  border: 0;
  box-shadow: none;
  width: 300px;
}

.flatpickr-current-month input.cur-year {
  appearance: none;
  padding: 0;
  margin: 0;
  background: none;
  pointer-events: none;
  -webkit-appearance: none;
  -moz-appearance:textfield;
}
.flatpickr-current-month span.cur-month {
  margin: 0;
}
.numInputWrapper:hover {
  background: none;
}
.numInputWrapper span.arrowUp, .numInputWrapper span.arrowDown {
  display: none;
}
.flatpickr-current-month .flatpickr-monthDropdown-months:hover,
.flatpickr-current-month span.cur-month:hover {
  background: transparent;
}

.flatpickr-months .flatpickr-month {
  width: auto;
  left: 0;
  padding: 0;
  height: auto;
  overflow: initial;
  padding-top: 0;
  margin-bottom: 14px;
  width: 100%;
}

.flatpickr-months .flatpickr-next-month, .flatpickr-months .flatpickr-prev-month {
  width: 24px;
  height: 24px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.flatpickr-months {
  position: relative;
}

.flatpickr-months .flatpickr-next-month.flatpickr-prev-month,
.flatpickr-months .flatpickr-prev-month.flatpickr-prev-month {
  left: auto;
  right: 28px;
  svg {
    display: none;
  }
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 12px;
    height: 12px;
    background-image: url(../img/date-l.svg);
  }
}
.flatpickr-current-month {
  position: static;
  width: auto;
  height: auto;
  padding: 0;
  display: flex;
  margin-right: auto;
  font-weight: bold;
  font-size: 20px;
  line-height: 120%;
  color: #252525;
}
.flatpickr-current-month .numInputWrapper {
  margin-left: 5px;
}
.flatpickr-current-month .numInputWrapper,
.flatpickr-current-month input.cur-year {
  width: max-content;
  font-weight: bold;
  width: 4ch;
}

.flatpickr-months .flatpickr-next-month.flatpickr-next-month,
.flatpickr-months .flatpickr-prev-month.flatpickr-next-month {
  svg {
    display: none;
  }
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 12px;
    height: 12px;
    background-image: url(../img/date-r.svg);
  }
}

span.flatpickr-weekday {
  font-size: 14px;
  line-height: 120%;
  color: #9E9E9E;
  text-transform: lowercase;
  display: block;
  font-weight: normal;
  &:not(:last-child) {
    margin-right: 1px;
  }
}
.flatpickr-rContainer,
.flatpickr-days {
  width: 100%;
}
.flatpickr-weekdays {
  height: 42px;
  padding-bottom: 10px;
  border-bottom: 1.5px dashed #E1E1E1;
  margin-bottom: 10px;
}

.flatpickr-weekday:nth-child(6) {
  color: #252525
}
.dayContainer {
  width: 100%;
  min-width: 100%;
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 1px;
}
.flatpickr-day {
  border: 0;
  width: 100%;
}

.flatpickr-day {
  max-width: 42px;
  line-height: 42px;
  height: 42px;
  font-size: 16px;
  color: #252525;
  border-radius: 6px;
}
.flatpickr-day.flatpickr-disabled, .flatpickr-day.flatpickr-disabled:hover, .flatpickr-day.nextMonthDay, .flatpickr-day.notAllowed, .flatpickr-day.notAllowed.nextMonthDay, .flatpickr-day.notAllowed.prevMonthDay, .flatpickr-day.prevMonthDay {
  color: #CACACA;
}

.flatpickr-day.endRange, .flatpickr-day.endRange.inRange, .flatpickr-day.endRange.nextMonthDay, .flatpickr-day.endRange.prevMonthDay, .flatpickr-day.endRange:focus, .flatpickr-day.endRange:hover, .flatpickr-day.selected, .flatpickr-day.selected.inRange, .flatpickr-day.selected.nextMonthDay, .flatpickr-day.selected.prevMonthDay, .flatpickr-day.selected:focus, .flatpickr-day.selected:hover, .flatpickr-day.startRange, .flatpickr-day.startRange.inRange, .flatpickr-day.startRange.nextMonthDay, .flatpickr-day.startRange.prevMonthDay, .flatpickr-day.startRange:focus, .flatpickr-day.startRange:hover {
  background: #252525;
}

.flatpickr-day.inRange, .flatpickr-day.nextMonthDay.inRange, .flatpickr-day.nextMonthDay.today.inRange, .flatpickr-day.nextMonthDay:focus, .flatpickr-day.nextMonthDay:hover, .flatpickr-day.prevMonthDay.inRange, .flatpickr-day.prevMonthDay.today.inRange, .flatpickr-day.prevMonthDay:focus, .flatpickr-day.prevMonthDay:hover, .flatpickr-day.today.inRange, .flatpickr-day:focus, .flatpickr-day:hover {
  background-color: #f5f5f5;
  border-color: #f5f5f5;
}
.flatpickr-day.endRange.startRange.endRange, .flatpickr-day.selected.startRange.endRange, .flatpickr-day.startRange.startRange.endRange {
  border-radius: 6px;
}
.flatpickr-day.endRange.startRange, .flatpickr-day.selected.startRange, .flatpickr-day.startRange.startRange {
  border-radius: 6px 0 0 6px;
}
.flatpickr-day.endRange.endRange, .flatpickr-day.selected.endRange, .flatpickr-day.startRange.endRange {
  border-radius: 0 6px 6px 0;
}

.flatpickr-day.inRange {
  box-shadow: 0 0 0 #f5f5f5,5px 0 0 #f5f5f5;
}
.flatpickr-day.endRange.startRange + .endRange:not(:nth-child(7n+1)), .flatpickr-day.selected.startRange + .endRange:not(:nth-child(7n+1)), .flatpickr-day.startRange.startRange + .endRange:not(:nth-child(7n+1)) {
  box-shadow: -5px 0 0 #f5f5f5;
}
.span.flatpickr-weekday {
  color: #CACACA;
}
@media(max-width: 767px) {
  .date-popup {
    padding: 0 30px;
    padding-bottom: 30px;
    width: 100%;
    position: fixed;
    min-height: 90vh;
    box-shadow: none;
    top: auto;
    bottom: 0;
    z-index: 1;
  }
  .filter__separator {
    display: none;
    + * {
      display: none;
    }
  }
  .flatpickr-days {
    display: flex;
    flex-direction: column;
    width: 100%!important;
  }
  .flatpickr-calendar.inline {
    max-width: 300px;
    margin: 0 auto;
  }
  .flatpickr-months .flatpickr-next-month, .flatpickr-months .flatpickr-prev-month {
    display: none;
  }
  .flatpickr-months .flatpickr-next-month, .flatpickr-months .flatpickr-prev-month {
    display: none;
  }
  .flatpickr-weekdays .flatpickr-weekdaycontainer:last-child {
    display: none;
  }
  .flatpickr-months,
  .flatpickr-rContainer,
  .flatpickr-innerContainer,
  .flatpickr-days {
    display: contents;
  }

  .date-scroll {
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-color: rgba(#252525, .5), #fff;
    scrollbar-width: thin;
    max-height: 540px;
    height: 100%;
    margin: 0 -30px;
    &::-webkit-scrollbar {
      width: 4px;
    }
    &::-webkit-scrollbar-track {
      background: #fff;
    }
    &::-webkit-scrollbar-thumb {
      background: rgba(#252525, .5);
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb:hover {
      background:#252525;
    }
  }
  .flatpickr-current-month {
    justify-content: center;
  }
  .flatpickr-calendar.inline {
    display: flex;
    flex-direction: column;
  }
  .flatpickr-weekdays {
    order: 1;
  }
  .flatpickr-months .flatpickr-month {
    order: 2;
  }
  .dayContainer {
    order: 3;
    margin-bottom: 26px;
  }
  .flatpickr-months .flatpickr-month + .flatpickr-month {
    order: 4;
  }
  .dayContainer + .dayContainer{
    order: 5;
    border: 0;
    box-shadow: none;
    margin-bottom: 0;
  }
  .filter-head--date {
    margin-bottom: 0;
  }
  .flatpickr-weekdays {
    margin-bottom: 26px;
  }
  .flatpickr-current-month {
    font-size: 16px;
    color: #454545;
  }
}
