
:root {
  --overlay-color: rgba(0, 0, 0, .6);
}

.modal {
  --overlay-color: rgba(0, 0, 0, .6);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--overlay-color);
  z-index: 100;
  overflow-x: hidden;
  overflow-y: auto;
  display: none;
  animation: fadeOut .15s both ease;
  cursor: pointer;
}
.modal--open {
  animation: fadeIn .15s both ease;
  display: block;
}

.modal__content {
  background-color: #ffffff;
  width: 100%;
  position: relative;
  visibility: hidden;
  opacity: 0;
  transition: .15s ease;
  cursor: default;
  max-width: 500px;
  margin: 0 auto;
  margin-top: 50px;
  background: #FFFFFF;
  box-shadow: 0px 25px 60px rgba(0, 0, 0, 0.07);
  border-radius: 16px;
  padding: 30px;
}
.modal__content--open {
  visibility: visible;
  opacity: 1;
}
.modal__close {
  --crossHeight: 20px;
  border: 0;
  padding: 0;
  background: 0;
  font-size: var(--crossHeight);
  font-weight: bold;
  width: var(--crossHeight);
  height: var(--crossHeight);
  line-height: var(--crossHeight);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 14px;
  top: 17px;
  cursor: pointer;
  transition: opacity .3s ease;
}

.search-form__wrap {
  position: relative;
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 15px;
    background-image: url(../img/gray-search.svg);
    width: 20px;
    height: 20px;
  }
}
.search-form__field {
  width: 100%;
  padding-left: 43px;
  padding-right: 43px;
  background: #FFFFFF;
  border: 1px solid #F5F5F5;
  border-radius: 6px;
  height: 40px;
  &::placeholder {
    color: #CACACA;
  }
}
.search-form__close {
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  cursor: pointer;
}

.search-form__request {
  margin-top: 28px;
  &.hidden {
    display: none;
  }
}
.search-title {
  font-size: 12px;
  line-height: 120%;
  color: #9E9E9E;
  display: block;
  margin-bottom: 16px;
}
.request-list {
  a {
    text-decoration: none;
    line-height: 120%;
    color: #252525;
    padding: 8px 30px;
    margin: 0 -30px;
    display: block;
    transition: .3s;
    position: relative;

    &:hover, &:focus {
      background: #F5F5F5;
    }
  }
  &--search {
    a {
      padding-left: 58px;
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 30px;
        background-image: url(../img/gray-search.svg);
        width: 20px;
        height: 20px;
      }
    }
  }
}
.search-form__result {
  display: none;
  margin-top: 28px;
  &.open {
    display: block;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@media(max-width: 575px) {
  .modal__content {
    margin-top: 0;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
}
