.promo {
  padding-top: 110px;
  padding-bottom: 110px;
}
.promo-item {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
  &:hover {
    .promo-item__text {
      display: -webkit-box;
    }
  }

}
.promo-item__text {
  display: none;
  font-size: 14px;
  line-height: 140%;
  color: rgba(255, 255, 255, 0.6);
  margin-top: 9px;
  margin-bottom: 0;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.promo-item__btn:hover + .promo-item__text {
  display: -webkit-box;

}
.promo__content {
  margin-top: 9px;
  margin-left: 113px;
}

.promo-item__btn {
  position: relative;
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 18px;
  line-height: 138.11%;
  color: #FFFFFF;
  cursor: pointer;
  &:hover {
    &::after {
      visibility: visible;
      opacity: 1;
    }
  }
  &::after {
    content: '';
    background-image: url(../img/arrow.svg);
    width: 24px;
    height: 24px;
    display: block;
    margin-left: 8px;
    transition: .3s;
    visibility: hidden;
    opacity: 0;
  }
}

@media(max-width: 1199px) {
  .promo__content {
    margin-left: 90px;
  }
}
@media(max-width: 991px) {
  .promo {
    padding-top: 28px;
    padding-bottom: 107px;
  }
  .promo__content {
    margin-left: 0;
    margin-top: 40px;
  }
}
@media(max-width: 767px) {
  .promo__title {
    font-size: 32px;
    line-height: 120%;
  }
  .promo-item__btn::after {
    content: unset;
  }
  .promo {
    padding-bottom: 100px;
  }
  .promo-item:not(:last-child) {
    margin-bottom: 16.5px;
  }
}
