.pe__tags {
  margin-top: 8px;
}

.pe-item {
  margin-right: 0;
}

.pe-item__title {
  &:hover {
    svg {
      visibility: visible;
      opacity: 1;
    }
  }
  svg {
    transition: .3s;
    visibility: hidden;
    opacity: 0;
    margin-top: -2px;
  }
}
@media(max-width: 767px) {
  .page__body--pp .main__wrapper {
    max-height: 540px;
  }
  br.only-mob {
    display: block;
  }
  .pe {
    padding-bottom: 0;
  }
}
