* {
	box-sizing: border-box;
	&:before {
		box-sizing: border-box;
	}
	&:after {
		box-sizing: border-box;
	}
}

*:focus {
	outline: none;
}

.page__body {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	height: 100%;
	font-family: var(--main-font);
	font-size: 14px;
}
.page-footer {
	margin-top: auto;
}

img {
	max-width: 100%;
	height: auto;
}

img, svg, iframe {
	vertical-align: top;
}

::placeholder {
	opacity: 1;
}

::selection {
	background-color:#000;
	color: #fff;
}
