.news__text {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  > span {
    margin-right: 12px;
  }
  .card__tags {
    margin-top: 0;
  }
}
.pp-item__news {
  margin-right: -3px;
}

@media(max-width: 767px) {
  .page__body--pn .main__wrapper{
    max-height: 450px;
  }
  .news__text {
    flex-direction: column;
    align-items: flex-start;
    > span {
      margin-bottom: 8px;
    }
  }
}
